@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    font-family: "Josefin Sans", sans-serif;

}

.main-heading {
    color: #5d4037;
    text-align: center;
}


.container{
    width: 90%;
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 3em;
    margin-bottom: 3em;
    display: grid;
    grid-template-columns: repeat(1,1fr);
    grid-gap: 30px;
}
.item{
    background-color: #fff ;
    cusor:pointer;
    box-shadow: 0 0 5px 0 rgba(0,0,0,.095);
    transition: all 0.3s ease-in-out;
}

.item:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.item-img{
    max-width: 100%;
    display: block;
}

@media screen and (min-width: 590px){
    .container{
        grid-template-columns: repeat(2,1fr);
    }
}

@media screen and (min-width: 960px){
    .container{
        grid-template-columns: repeat(3,1fr);
    }
}

.formii button{
    padding:1.5rem 2rem ;
    background: #3b89b6;
    border:0.1rem solid #4896c4;
    cursor:pointer;
    position: relative;
    border-radius: .1rem;
    overflow: hidden;

}

.formii{
    text-decoration: none;
    padding: 20px;
    display: block;
    text-align: center;
}
.container {
    width: 85%;
    max-width: 1000px;
    margin: 0 auto;
}

.header{
    background: #F0F7F1;
    color: black;
    padding: 2rem 0;
}

.header a{
    color:black;
    text-decoration: none;
}

.logo-nav-container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;

}

.logo {
    letter-spacing: 5px;
    font-size:1.3em;
}



.logo img{
    width:350px;
    height: 80px;
    margin-left: 0;

}

.navigation ul {
    margin:0;
    padding :0;
    list-style: none;


}

.navigation ul li{
    display: inline-block;
}

.navigation ul li a{
    display:block;
    padding: 0.5rem 1rem ;
    transition: all 0.4s linear;
    border-radius: 5px;

}

.navigation ul li a:hover{
    background: gray;

}
